import { Button, Typography } from "@material-ui/core";
import { Link, useHistory, useParams } from "react-router-dom";
import Otsikko from "./Otsikko";

function Rekisteripoisto(props) {
  const history = useHistory();
  const { id } = useParams();

  const poistaMerkinta = () => {
    let rekisteriApu = props.rekisteri.filter((rekisteri) => {
      return rekisteri.id !== id;
    });

    props.setRekisteri([...rekisteriApu]);

    history.push("/");
  };

  return (
    <>
      <Otsikko tyyli="pieni">Poista rekisterimerkintä</Otsikko>

      <Typography>
        Haluatko varmasti poistaa rikosrekisterimerkinnän henkilöltä "
        {
          props.rekisteri.filter((henkilo) => {
            return henkilo.id === id;
          })[0].henkilo
        }
        "?
      </Typography>

      <Button
        variant="contained"
        color="primary"
        fullWidth
        size="large"
        onClick={poistaMerkinta}
        style={{ marginTop: 10 }}
      >
        Poista rekisterimerkintä
      </Button>

      <Button
        component={Link}
        to="/"
        variant="contained"
        color="default"
        fullWidth
        size="large"
        style={{ marginTop: 10 }}
      >
        Peruuta
      </Button>
    </>
  );
}

export default Rekisteripoisto;
