import { Link } from "react-router-dom";
import {
  Button,
  IconButton,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
} from "@material-ui/core";
import DeleteIcon from "@material-ui/icons/Delete";
import Otsikko from "./Otsikko";
import { format } from "date-fns";

function Rekisterilista(props) {
  return (
    <>
      <Otsikko>Rikosrekisteri</Otsikko>

      <Button
        component={Link}
        to="/uusi"
        variant="contained"
        color="primary"
        fullWidth
        size="large"
      >
        Lisää uusi rekisterimerkintä
      </Button>
      <Button
        fullWidth
        component={Link}
        to="/haku"
        variant="contained"
        color="primary"
        style={{ marginTop: 15 }}
        size="large"
      >
        RekisteriHaku
      </Button>

      <List>
        {props.rekisteri.map((merkinta, idx) => {
          return (
            <ListItem key={idx}>
              <ListItemText primary={"Henkilö : " + merkinta.henkilo} />
              <ListItemText primary={"Rikosnimike : " + merkinta.rikos} />
              <ListItemText primary={"Henkilötunnus : " + merkinta.hetu} />
              <ListItemText
                primary={
                  "Merkintä Aika :  " + format(merkinta.aika, "d.M.y HH:mm")
                }
              />
              <ListItemIcon>
                <IconButton component={Link} to={`/poista/${merkinta.id}`}>
                  <DeleteIcon />
                </IconButton>
              </ListItemIcon>
            </ListItem>
          );
        })}
      </List>
    </>
  );
}

export default Rekisterilista;
