import {
  Button,
  Container,
  ListItem,
  ListItemText,
  TextField,
} from "@material-ui/core";
import React, { useState } from "react";
import { Link } from "react-router-dom";
import Otsikko from "./Otsikko";
import { format } from "date-fns";

function Rekisterihaku(props) {
  const [haettava, setHaettava] = useState("");
  const [hakuTulokset, setHakuTulokset] = useState([]);

  const haeRekisterista = () => {
    const hakuApu = props.rekisteri.filter((rekisteri) => {
      if (haettava) {
        return (
          rekisteri.henkilo.toLowerCase().includes(haettava.toLowerCase()) ||
          rekisteri.rikos.toLowerCase().includes(haettava.toLowerCase())
        );
      } else return;
    });
    setHakuTulokset([...hakuApu]);
  };

  return (
    <Container>
      <Otsikko tyyli="pieni">Hae rekisteristä</Otsikko>

      <TextField
        variant="outlined"
        fullWidth
        placeholder="Hae Henkilö tai Rikos"
        style={{ marginBottom: 10 }}
        onChange={(e) => {
          setHaettava(e.target.value);
        }}
      />

      <Button
        variant="contained"
        color="primary"
        fullWidth
        size="large"
        style={{ marginTop: 10 }}
        onClick={haeRekisterista}
      >
        Hae
      </Button>
      <Button
        component={Link}
        to="/"
        variant="contained"
        color="primary"
        fullWidth
        size="large"
        style={{ marginTop: 10 }}
      >
        takaisin
      </Button>
      {hakuTulokset.map((item, idx) => {
        return (
          <ListItem key={idx}>
            <ListItemText primary={"Henkilö : " + item.henkilo}></ListItemText>
            <ListItemText primary={"Rikos : " + item.rikos}></ListItemText>
            <ListItemText
              primary={"Henkilötunnus : " + item.hetu}
            ></ListItemText>
            <ListItemText
              primary={"Merkintä Aika : " + format(item.aika, "d.M.y HH:mm")}
            ></ListItemText>
          </ListItem>
        );
      })}
    </Container>
  );
}

export default Rekisterihaku;
