import { Button, TextField, Container } from "@material-ui/core";
import { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import Otsikko from "./Otsikko.js";
import { v4 as uuid } from "uuid";

function Uusimerkinta(props) {
  const [uusiHenkilo, setUusiHenkilo] = useState();
  const [uusiRikos, setUusiRikos] = useState();
  const [pvmAika, setPvmAika] = useState(new Date());
  const [uusiHetu, setUusiHetu] = useState();
  const history = useHistory();

  const lisaaMerkinta = () => {
    let rekisteriApu = {
      id: uuid(),
      henkilo: uusiHenkilo,
      rikos: uusiRikos,
      hetu: uusiHetu,
      aika: pvmAika.getTime(),
    };

    props.setRekisteri([...props.rekisteri, rekisteriApu]);

    history.push("/");
  };

  return (
    <Container>
      <Otsikko tyyli="pieni">Lisää uusi Rekisterimerkintä</Otsikko>

      <TextField
        variant="outlined"
        fullWidth
        placeholder="Henkilö"
        style={{ marginBottom: 10 }}
        onChange={(e) => {
          setUusiHenkilo(e.target.value);
        }}
      />
      <TextField
        variant="outlined"
        fullWidth
        placeholder="Rikos"
        style={{ marginBottom: 10 }}
        onChange={(e) => {
          setUusiRikos(e.target.value);
        }}
      />
      <TextField
        variant="outlined"
        fullWidth
        placeholder="Henkilötunnus"
        style={{ marginBottom: 10 }}
        onChange={(e) => {
          setUusiHetu(e.target.value);
        }}
      />

      <Button
        variant="contained"
        color="primary"
        fullWidth
        size="large"
        onClick={lisaaMerkinta}
        style={{ marginTop: 10 }}
      >
        Tallenna
      </Button>

      <Button
        component={Link}
        to="/"
        variant="contained"
        color="default"
        fullWidth
        size="large"
        style={{ marginTop: 10 }}
      >
        Peruuta
      </Button>
    </Container>
  );
}

export default Uusimerkinta;
