import { useState, useEffect } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Rekisteripoisto from "./Components/Rekisteripoisto";
import Uusimerkinta from "./Components/Uusimerkinta";
import Rekisterilista from "./Components/Rekisterilista";
import Rekisterihaku from "./Components/Rekisterihaku";

function App() {
  const [rekisteri, setRekisteri] = useState([]);
  const [rekisteriHaku, setRekisteriHaku] = useState();

  const tallennaTiedot = () => {
    localStorage.setItem("Rikosrekisteri", JSON.stringify(rekisteri));
  };

  const avaaRikosrekisteri = () => {
    if (localStorage.getItem("Rikosrekisteri")) {
      setRekisteri(JSON.parse(localStorage.getItem("Rikosrekisteri")));
    } else {
      setRekisteri([]);
    }
  };

  useEffect(() => {
    avaaRikosrekisteri();
  }, []);

  useEffect(() => {
    tallennaTiedot();
  }, [rekisteri]);

  return (
    <Router>
      <Route path="/" exact>
        <Rekisterilista
          rekisteri={rekisteri}
          setRekisteri={setRekisteri}
          tallennaTiedot={tallennaTiedot}
        />
      </Route>

      <Route path="/uusi">
        <Uusimerkinta rekisteri={rekisteri} setRekisteri={setRekisteri} />
      </Route>

      <Route path="/poista/:id">
        <Rekisteripoisto rekisteri={rekisteri} setRekisteri={setRekisteri} />
      </Route>
      <Route path="/haku" exact>
        <Rekisterihaku
          rekisteri={rekisteri}
          setRekister={setRekisteri}
          rekisteriHaku={rekisteriHaku}
          setRekisteriHaku={setRekisteriHaku}
        />
      </Route>
    </Router>
  );
}

export default App;
